<template>
    <div class="app">
        <ul class="ov nav">
            <li class="fl"><a href="javascript:void (0)"  class="pr" @click="changeNav(1)" :class="{active: active == 1}">充值记录<span></span></a></li>
            <li class="fl"><a href="javascript:void (0)" class="pr"  @click="changeNav(2)" :class="{active: active == 2}">提币记录<span></span></a></li>
        </ul>
        <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
                v-show="active == 1"
        >
            <div class="dateList">
                <ul>
                    <li v-for="item in listData" class="ov">
                        <a href="javascript:void(0);" class="fl">
                            <p class="title">{{item.coinType}}</p>
                            <p class="btnBox"><span class="time">{{item.create_time}}</span><span class="suBtn">充值成功</span></p>
                        </a>
                        <p class="fl numBox">数量: {{item.count}}</p>
                    </li>
                </ul>
            </div>
        </van-list>
        <van-list
                v-model="loading_cash"
                :finished="finished_cash"
                finished-text="没有更多了"
                @load="onLoadCash"
                v-show="active == 2"
        >
            <div class="dateList">
                <ul>
                    <li v-for="item in listCashData" class="ov">
                        <a href="javascript:void(0);" class="fl">
                            <p class="title">{{item.currency}}</p>
                            <p class="btnBox"><span class="time">{{item.create_time}}</span><span class="suBtn">{{item.state}}</span></p>
                        </a>
                        <p class="fl numBox">数量: {{item.total}}</p>
                    </li>
                </ul>
            </div>
        </van-list>
    </div>
</template>

<script>
    import { rechargeLog, cashLog } from "@/api/user"
    import { List } from "vant";
    import 'vant/lib/list/style';
    export default {
        name: "Rechargelog",
        components: {
            [List.name]: List,
        },
        data(){
            return {
                active:1,
                loading: false,
                finished: false,
                listData:[], // 币种列表
                page:1,
                currency_id:'',
                loading_cash: false,
                finished_cash: false,
                listCashData:[], // 币种列表
                page_cash:1,
            }
        },
        mounted()
        {
            this.currency_id = this.$route.query.currencyId
            // this.getList()
        },
        methods:{

            changeNav(num)
            {
                this.active = num
                // 第一次点击拉取数据
                if (num == 2 && this.page_cash == 1)
                {
                    this.getCashList()
                }

            },
            // 获取充值列表
            getList(){

                rechargeLog({page:this.page, currency_id:this.currency_id}).then(res => {

                    this.page++
                    this.loading = false
                    this.listData = this.listData.concat(res)
                    if (res.length < 10)
                    {
                        this.finished = true
                    }
                })
            },
            // 获取提现数据
            getCashList()
            {
                cashLog({page:this.page_cash, currency_id:this.currency_id}).then(res => {

                    this.page_cash++
                    this.loading_cash = false
                    this.listCashData = this.listCashData.concat(res)
                    if (res.length < 10)
                    {
                        this.finished_cash = true
                    }
                })
            },
            // 加载
            onLoad() {
                this.loading = true
                this.getList()
            },
            onLoadCash()
            {
                this.loading_cash = true
                this.getCashList()
            }
        }
    }
</script>

<style scoped>
    @import "../../../static/css/rechargeLog.css";
</style>



